import { createSlice } from "@reduxjs/toolkit";
import axios from "axios";

export type State = {
  accessToken: string | null;
  isSuper: boolean;
};

const initialState: State = {
  accessToken: null,
  isSuper: false,
};

// eslint-disable-next-line @typescript-eslint/explicit-module-boundary-types
const authSlice = (name: string) =>
  createSlice({
    name,
    initialState,
    reducers: {
      initialize(state) {
        if (state.accessToken !== null) {
          axios.defaults.headers.common.Authorization = state.accessToken;
        }
      },
      login(
        state,
        action: { payload: { accessToken: string; isSuper: boolean } }
      ) {
        state.accessToken = action.payload.accessToken;
        state.isSuper = action.payload.isSuper;
        axios.defaults.headers.common.Authorization =
          action.payload.accessToken;
      },
      setIsSuper(state, action: { payload: { isSuper: boolean } }) {
        state.isSuper = action.payload.isSuper;
      },
      logout(state) {
        state.accessToken = null;
        state.isSuper = false;
        delete axios.defaults.headers.common.Authorization;
      },
    },
  });
export default authSlice;
